.project {
    width: 100%;
    height: calc(100vh - 100px);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.project h1 {
    margin-top: 50px;
    color: #3e497a;
    font-size: 35px;
}

.project img {
    width: 400px;
    height: 400px;
    border-radius: 10px;

}

.project p,
ul {
    font-size: 20px;
    color: #3e497a;
    margin: 30px 150px 50px 150px;
}

.project svg {
    font-size: 60px;
    color: #3e497a;
}